.feature-section {
    background-color: #f8f9ff;
    padding: 4rem 1rem;
    text-align: center;
    width: 100%;
  box-sizing: border-box;
  }
  .feature-header {
    max-width: 600px;
    margin: 0 auto 2.5rem auto;
    padding: 0 1rem;
  }
  
  .feature-header h2 {
    font-size: 2.2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
    color: #111;
    line-height: 1.3;
  word-break: break-word;
  }
  
  .feature-header p {
    font-size: 1.125rem;
    color: #666;
    margin-bottom: 3rem;
    line-height: 1.6;
  word-break: break-word;
  }
  
  .feature-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    justify-items: stretch;
    align-items: stretch;
    padding: 0 1rem;
    box-sizing: border-box;
  }
  
  /* .feature-card {
    background: white;
    border-radius: 12px;
    padding: 2rem;
    box-shadow: 0 5px 22px rgba(0,0,0,0.05);
    transition: transform 0.3s ease;
    text-align: left;
  } */
  .feature-card {
    background: transparent;
    padding: 1rem;
    box-shadow: none;
    border-radius: 0;
    transition: none;
    text-align: left;
  }
  
  .feature-card h3 {
    font-size: 1.2rem;
    font-weight: 600;
    color: #111;
    margin-bottom: 0.5rem;
    word-break: break-word;
  }
  .feature-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 32px rgba(0, 0, 0, 0.08);
  }
  
  .feature-card p {
    font-size: 0.95rem;
    color: #555;
    line-height: 1.6;
    word-break: break-word;
  }
  .feature-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  }
  
  .feature-icon {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #4a4a4a;
  }
  @media (max-width: 1024px) {
    .feature-header h2 {
      font-size: 2rem;
    }
  
    .feature-header p {
      font-size: 1.05rem;
      margin-bottom: 2.5rem;
    }
  }
  
  @media (max-width: 768px) {
    .feature-section {
      padding: 3rem 1rem;
    }
  
    .feature-header h2 {
      font-size: 1.75rem;
    }
  
    .feature-header p {
      font-size: 1rem;
    }
  
    .feature-card {
      padding: 1.2rem;
    }
  
    .feature-card h3 {
      font-size: 1.1rem;
    }
  
    .feature-card p {
      font-size: 0.9rem;
    }
  }
  
  @media (max-width: 480px) {
    .feature-section {
      padding: 2.5rem 0.8rem;
    }
  
    .feature-header h2 {
      font-size: 1.5rem;
    }
  
    .feature-header p {
      font-size: 0.95rem;
    }
  
    .feature-grid {
      gap: 1.5rem;
      grid-template-columns: 1fr;
    }
  
    .feature-card {
      padding: 1rem;
    }
  
    .feature-card h3 {
      font-size: 1rem;
    }
  
    .feature-card p {
      font-size: 0.875rem;
    }
  
    .feature-icon {
      font-size: 1.8rem;
    }
  }